.def-list {
    &__row {
        display: flex;
        padding-bottom: 10px;
        border-bottom: 1px solid #000;
        margin-bottom: 10px;
        &:last-of-type {
            margin-bottom: 0;
            border-bottom: none;
        }
    }
    &__term {
        font-weight: bold;
        white-space: nowrap;
    }
}