@use '../base/mediaquery' as md;

.fv {
    min-height: 300px;
    height: 100vh;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    color: #fff;
    position: relative;
    &__content {
        position: absolute;
        top: 40%;
        left: 5%;
        transform: translateY(-50%);
        @include md.tab {
            top: 45%;
            left: 10%;
        };
    }
    &__text {
        font-family: 'PT Sans', sans-serif;
        font-size: 4.8rem;
        font-weight: bold;
        text-shadow: (0 3px 6px rgba(0, 0, 0, 0.30));
        @include md.tab {
            font-size: 6.4rem;
        };
    }
    &__arrow {
        position: absolute;
        bottom: 2px;
        right: 10px;
    }
}
.fv--page {
    height: 200px;
    @include md.tab {
        height: 300px;
    };
    @include md.pc {
        background-position: center 60%;
    };
    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .6);
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
    }
    .fv__content {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 20;
    }
    .fv__text {
        font-size: 3.6rem;
        @include md.tab {
            font-size: 4.8rem;
        };
    }
}