.contact {
    color: #fff;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    position: relative;
    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .6);
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
    }
    &__section-inner {
        position: relative;
        z-index: 20;
    }
}