@use './../base/mediaquery' as md;

.card {
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
    position: relative;
    &__img-wrapper {
        position: relative;
        padding-top: 56.25%;
        overflow: hidden;
    }
    &__img {
        position: absolute;
        top: 50%;
        width: 100%;
        height: 100%;
        transform: translateY(-50%);
    }
    &__body {
        padding: 16px 18px 24px 18px;
        > *:last-child {
            margin-bottom: 0;
        }
    }
    &__title {
        margin-bottom: 3px;
        font-size: 1.8rem;
        font-weight: bold;
    }
    &__text {
        margin-bottom: 16px;
        font-family: "游ゴシック体", "YuGothic", "游ゴシック", "Yu Gothic", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
    }
}
.cards {
    margin-bottom: -30px;
    @include md.tab {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    };
    .card {
        margin-bottom: 30px;
        @include md.tab {
            flex-basis: 30%;
            &__body {
                padding-bottom: 60px;
            }
            &__gradient-btn {
                position: absolute;
                bottom: 16px;
            }
        };
    }
}