@use './../base/mediaquery' as md;

.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    transition: .3s;
    &__inner {
        padding-right: 20px;
        padding-left: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 50px;
        @include md.tab {
            height: 80px;
        };
    }
    &__nav {
        display: none;
        font-size: 1.8rem;
        @include md.tab {
            display: block;
        };
    }
    &.white {
        color: #333;
        background-color: #fff;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16);
        .site-title__text {
            color: #333;
        }
        .horizontal-nav a {
            color: #333;
        }
    }
}