html {
  font-size: 62.5%;
}

body {
  line-height: 1.6;
  font-size: 1.6em;//必ずem!!
  font-family: 'PT Sans', "游ゴシック体", "YuGothic", "游ゴシック", "Yu Gothic", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
  color: #333;
  background-color: #fff;
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

a {
  text-decoration: none;
  outline: none;
  color: #333;
}

li {
    list-style: none;
}

button {
  border: none;
  background-color: transparent;
}
  
textarea,
input[type="text"],
input[type="tel"],
input[type="password"] {
  // iOSのフィールド内側のシャドウを無くす
  appearance: none;
  //リサイズ禁止
  resize: none;
}
  
input::-ms-clear,
input::-ms-reveal {
  // IE11のフィールドの×ボタンを非表示にする
  visibility: hidden;
}