@use '../base/mediaquery' as md;

.humburger-btn {
    width: 50px;
    height: 50px;
    padding: 0;
    border: none;
    background-color: transparent;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 120;
    cursor: pointer;
    @include md.tab {
        display: none;
    };
    .humburger-btn__line {
        width: 30px;
        height: 2px;
        background-color: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: background-color .3s;
        &::before {
            content: '';
            width: 30px;
            height: 2px;
            background-color: #fff;
            position: absolute;
            top: -10px;
            left: 50%;
            transform: translateX(-50%);
            transition: transform .3s;
            transition: background-color .3s;
        }
        &::after {
            content: '';
            width: 30px;
            height: 2px;
            background-color: #fff;
            position: absolute;
            top: 10px;
            left: 50%;
            transform: translateX(-50%);
            transition: transform .3s;
            transition: background-color .3s;
        }
        &.black {
            background-color: #333;
            &::before,
            &::after {
                background-color: #333;
            }
        }
    }
}

.humburger-btn.cross {
    .humburger-btn__line {
        background-color: rgba(0,0,0,0);
        transition: transform .3s;
        &::before {
            background-color: #fff;
            transform: translate(-50%,11px) rotate(45deg);      
            transition: transform .3s;
            z-index: 120;
        }
        &::after {
            background-color: #fff;
            transform: translate(-50%,-9px) rotate(-45deg);      
            transition: transform .3s;
            z-index: 120;
        }
    }
}

.humburger-nav-wrapper {
    width: 70%;      
    height: 100vh;      
    position: fixed;
    top: 0;      
    right: -100%;      
    z-index: 110;
    transition: .5s;
    &.open {
        right: 0;
    }
    .humburger-nav {
        width: 100%;        
        height: 100%;        
        background-color: rgba(0,0,0,0.8);       
        .humburger-nav__list {
            display: block;        
            position: absolute;        
            top: 50%;        
            left: 50%;        
            transform: translate(-50%,-50%);        
            text-align: center;
            .humburger-nav__item {
                margin-bottom: 20px;
                &:last-child {
                    margin-bottom: 0;
                }
                a {
                    color: #fff;
                    font-size: 1.8rem;
                }
            }
        }
    }
}