@use '../base/mediaquery' as md;

.about {
    &__section-title {
        margin-bottom: 60px;
        @include md.tab {
            margin-bottom: 80px;
        };
    }
    @include md.tab {
        &__inner {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    };
    &__img-wrapper {
        margin: 20px auto 20px;
        max-width: 450px;
        width: 70%;
        position: relative;
        @include md.tab {
            max-width: 300px;
            flex-basis: 40%;
            margin-right: 10%;
            margin-bottom: 0;
            width: 100%;
        };
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 360px;
            background-color: #333;
            position: absolute;
            top: -20px;
            left: -20px;
            z-index: 1;
        }
    }
    &__img {
        width: 100%;
        height: 360px;
        object-fit: cover;
        object-position: 50% bottom;
        position: relative;
        z-index: 2;
    }
    @include md.tab {
        &__content {
            flex-basis: 50%;
        }
    };
    &__text {
        margin-bottom: 20px;
        font-family: "游ゴシック体", "YuGothic", "游ゴシック", "Yu Gothic", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
        @include md.tab {
            margin-bottom: 30px;
            font-size: 2rem;
        };
    }
}