@use '../base/mediaquery' as md;

.sns-link {
    display: block;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
    &--twitter {
        border: 2px solid rgb(29, 155, 240);
        background-image: url('./../images/twitter-blue.png');
        background-size: 32px;
        background-position: center;
        background-repeat: no-repeat;
        &:hover {
            background-image: url('./../images/twitter-white.png');
            background-color: rgb(29, 155, 240);
        }
    }
    &--mail {
        border: 2px solid #333;
        position: relative;
        cursor: pointer;
        i {
            font-size: 3.2rem;
            color: #333;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        &:hover {
            background-color: #333;
            i {
                color: #fff;
            }
        }
    }
}
.sns-links {
    display: flex;
    justify-content: center;
    .sns-link {
        margin-right: 20%;
        @include md.tab {
            margin-right: 10%;
        };
        &:last-of-type {
            margin-right: 0;
        }
    }
}
