@use '../base/mediaquery' as md;

.work-detail {
    &__video {
        margin-bottom: 30px;
        @include md.tab {
            margin-bottom: 50px;
        };
    }
    &__def-list {
        margin-bottom: 20px;
        font-family: "游ゴシック体", "YuGothic", "游ゴシック", "Yu Gothic", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
        @include md.tab {
            margin-bottom: 40px;
        };
        @include md.pc {
            font-size: 2rem;
        };
    }
    &__gradient-btn-wrapper {
        margin-bottom: 40px;
        @include md.tab {
            margin-bottom: 60px;
        };
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}