@use './../base/mediaquery' as md;

.modal {
    z-index: 900;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 60px 10px;
    text-align: center;
    &:not(:target) {
        opacity: 0;
        visibility: hidden;
        transition: opacity .3s, visibility .3s;
    }
    &:target {
        opacity: 1;
        visibility: visible;
        transition: opacity .4s, visibility .4s;
    }
    &::after {
        display: inline-block;
        height: 100%;
        margin-left: -.05em;
        vertical-align: middle;
        content: ""
    }
    &__window {
        box-sizing: border-box;
        display: inline-block;
        z-index: 20;
        position: relative;
        width: 70%;
        max-width: 600px;
        padding: 30px 30px 15px;
        border-radius: 6px;
        background: #fff;
        box-shadow: 0 0 30px rgba(0, 0, 0, .6);
        vertical-align: middle;
        @include md.tab {
            padding: 50px 40px 30px;
        };
    }
    &__content {
        max-height: 80vh;
        overflow-y: auto;
    }
    &__heading {
        margin-bottom: 5px;
        color: #333;
        font-size: 2rem;
        @include md.tab {
            font-size: 2.8rem;
        };
    }
    &__text {
        color: #333;
        @include md.tab {
            font-size: 2rem;
        };
    }
    &__overlay {
        z-index: 10;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, .8);
    }
    &__close {
        z-index: 20;
        position: absolute;
        top: 0;
        right: 0;
        width: 35px;
        color: #95979c !important;
        font-size: 20px;
        font-weight: 700;
        line-height: 35px;
        text-align: center;
        text-decoration: none;
        text-indent: 0;
        @include md.tab {
            font-size: 4rem;
        };
        &:hover {
            color: #333 !important;
        } 
    }
}