@use '../base/mediaquery' as md;

.section-title {
    margin-bottom: 40px;
    @include md.tab {
        margin-bottom: 60px;
        text-align: center;
    };
    &__text {
        display: inline-block;
        font-family: 'Lobster', cursive;
        padding-right: 2em;
        font-size: 3.2rem;
        letter-spacing: 0.1em;
        position: relative;
        @include md.tab {
            padding-right: 0;
            font-size: 4.2rem;
        };
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 2px;
            background-color: currentColor;
            position: absolute;
            @include md.tab {
                width: 56px;
                left: 50%;
                transform: translateX(-50%);
            };
        }
    }
}
.section-title--reverse {
    text-align: right;
    @include md.tab {
        text-align: center;
    };
    .section-title__text {
        padding-right: 0;
        padding-left: 2em;
        position: relative;
        @include md.tab {
            padding-left: 0;
            font-size: 4.2rem;
        };
        &::after {
            position: absolute;
            right: 0;
            @include md.tab {
                left: 50%;
            };
        }
    }
}