.skill {
    padding: 20px;
    width: 130px;
    height: 130px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 2px 2px 6px #000;
    text-align: center;
    transition: .2s;
    &:hover {
        box-shadow: none;
        transform: translate(2px, 2px);
    }
    .skill__img-wrapper {
        display: inline-block;
    }
    .skill__img {
        width: 60px;
        height: 60px;
    }
    .skill__title {
        color: #333;
        font-weight: bold;
    }
}
.skills {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .skill {
        margin: 5%;
    }
}