@use './../base/mediaquery' as md;

.breadcrumb {
    width: 100%;
    padding-top: 17px;
    padding-bottom: 17px;
    background-color: #F8F8F8;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    &__inner {
        margin: 0 auto;
        padding-right: 20px;
        padding-left: 20px;
        width: 100%;
        max-width: 1260px;
        @include md.tab {
            padding-right: 30px;
            padding-left: 30px;
        };
    }
    a {
        text-decoration: underline;
        color: #1B224C;
        &:hover {
            text-decoration: none;
        }
    }
    &__item {
        margin-right: 4px;
        margin-left: 4px;
        &:first-of-type {
            margin-left: 0;
        }
    }
}