.gradient-btn {
    display: inline-block;
    width: 140px;
    max-width: 100%;
    padding: 4px 12px;
    border-radius: 10px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
    color: #fff;
    font-size: 1.4rem;
    text-align: center;
    position: relative;
    &::before,
    &::after {
        content: '';
        display: inline-block;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        position: absolute;
        top: 0;
        left: 0;
    }
    &::before {
        background: linear-gradient(to right, #002f54, #145e8f);
        transition: .5s;
        z-index: 20;
    }
    &::after {
        background: linear-gradient(to left, #002f54, #145e8f);
        z-index: 10;
    }
    &__content {
        position: relative;
        z-index: 30;
    }
    &:hover::before {
        opacity: 0;
    }
    &--large {
        width: 200px;
        padding: 9px 12px;
        border-radius: 40px;
        font-size: 1.6rem;
        .gradient-btn::before,
        .gradient-btn::after {
            border-radius: 40px;
        }
    }
}
.gradient-btn-wrapper {
    text-align: center;
    &--mt60 {
        margin-top: 60px;
    }
}
.gradient-btn--pink {
    &::before {
        background: linear-gradient(to right, #002f54, #ee0b75);
    }
    &::after {
        background: linear-gradient(to left, #002f54, #ee0b75);
    }
}