@use './../base/mediaquery' as md;

.price {
    background-color: #002f54;
    color: #fff;
    &__horizontal-table {
        margin: 0 auto 10px;
        max-width: 800px;
        font-family: "游ゴシック体", "YuGothic", "游ゴシック", "Yu Gothic", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
        @include md.tab {
            margin-bottom: 15px;
            font-size: 2rem;
        };
    }
    @include md.tab {
        &__note-text {
            font-size: 1.6rem;
            text-align: center;
        }
    };
}