.contact-form {
    margin: 0 auto;
    max-width: 700px;
    &__item {
        margin-bottom: 14px;
        &--radio {
            margin: 18px 0 24px;
        }
        &:last-of-type {
            margin-bottom: 30px;
        }
    }
    &__label {
        margin-bottom: 6px;
        display: block;
        color: rgba(255, 255, 255, 0.8);
    }
    &__input {
        display: block;
        width: 100%;
        padding: 10px 20px;
        outline: none;
        border: 1px solid rgba(255, 255, 255, 0.4);
        color: #fff;
        background-color: rgba(255, 255, 255, 0.05);
        &:focus {
            box-shadow: 0 0 8px rgba(255, 255, 255, 0.5) inset;
        }
    }
    &__textarea {
        display: block;
        width: 100%;
        height: 200px;
        padding: 16px 20px; 
        outline: none;
        border: 1px solid rgba(255, 255, 255, 0.4);
        color: #fff;
        background-color: rgba(255, 255, 255, 0.05);
        &:focus {
            box-shadow: 0 0 8px rgba(255, 255, 255, 0.5) inset;
        }
    }
    &__submit {
        display: block;
        width: 180px;
        margin: 0 auto;
        padding: 10px;
        border: 1px solid rgba(255, 255, 255, 0.8);
        border-radius: 5px;
        background-color: transparent;
        color: rgba(255, 255, 255, 0.8);
        cursor: pointer;
        &:hover {
            background-color: rgba(255, 255, 255, 0.05);
        }
        &.disable {
            opacity: .3;
            cursor: auto;
            &:hover {
                background-color: transparent;
            }
        }
    }
}